import React, { Fragment, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Menu, Dropdown, notification } from 'antd';
import CustomCellLayout from './CustomCellLayout';
import response from './../../../helper/response';
import ConfigData from './ResCleanedTableConfig.json';
import RealEstateServices from '../../../services/post';
import CleanedResDataTable from './CleanedResDataTable';
import { ENDPOINTS } from '../../../constants/endpoints';
import { REQUEST_CONFIG } from '../../../constants/request';
import requestService from "../../../services/request";
import { ReactComponent as ChevronDown } from '../../../assets/images/icons/chevron-down.svg';
import { ReactComponent as ChevronSort } from '../../../assets/images/icons/chevrons-sort.svg';
import { ReactComponent as ThreeDot } from '../../../assets/images/icons/three_dot.svg';
import { ReactComponent as CheckCircle } from '../../../assets/images/icons/check-circle.svg';
import { injectIntl, FormattedMessage } from 'react-intl';
import 'antd/dist/antd.css';
import '@styles/base/pages/page-res-cleaned.scss';

// Handle event when click on multi actions dropdown
const onClick = ({ item, key, keyPath, domEvent }, record, setItemRemoved = () => {}) => {
    requestService.send({
        method: "POST",
        path: "RealEstateRaw/pickCleaned",
        data: {
            pickedListId: [record.id],
        }
    }).then(() => {
        notification.info({
            message: "Res moved to Picked list",
        });
        setItemRemoved(prev => !prev);
    });
};

const actionMenu = (record, setItemRemoved = () => {}) => (
    <Menu onClick={(initial) => onClick(initial, record, setItemRemoved)}>
        <Menu.Item key='1'>
            <CheckCircle className='check-circle-icon'/>
            <FormattedMessage id='pickRes'/>
        </Menu.Item>
    </Menu>
);

// Format VND currency
const formatCurrency = (value) => {
    return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(value)
}

const ResCleanedList = () => {

    const [itemRemoved, setItemRemoved] = useState(false);

    const columns = [
        {
            title: 
                <div>
                    <ChevronSort className='chevron-sort-icon'/>
                        <FormattedMessage id='id'/>
                    <ChevronDown className='mg-left-10'/>
                </div>,
            dataIndex: 'id',
            render: (id) => <Link to={`/view/${id}`} replace>#{id?.toString().padStart(6, 0)}</Link>,
            onCell: (_) => ({
                style: {
                    color: 'blue',
                    textDecoration: 'underline',
                    cursor: 'pointer'
                },
            }),
            sortable: true,
            compact: true,
            width: '50px'
        },
        {
            title: 
                <div>
                    <span><FormattedMessage id='address'/></span>
                    <ChevronDown className='mg-left-10'/>
                </div>,
            dataIndex: 'address',
            render: (item) => <CustomCellLayout item={item} layout={{cols: 1, isMultipleRow: false, reverseData: false}}/>,
            onCell: (_) => ({
                className: 'address',
            }),
            width: '100px'
        },
        {
            title: 
                <div>
                    <span><FormattedMessage id='land'/></span>
                    <ChevronDown className='mg-left-10'/>
                </div>,
            dataIndex: 'land',
            render: (item) => (
                <CustomCellLayout item={item} layout={{cols: 2, isMultipleRow: false, reverseData: false}}/>
            ),
            onCell: (_) => ({}),
            width: '100px'
        },
        {
            title: 
                <div>
                    <span><FormattedMessage id='house'/></span>
                    <ChevronDown className='mg-left-10'/>
                </div>,
            dataIndex: 'house',
            render: (item) => (
                <CustomCellLayout item={item} layout={{cols: 1, isMultipleRow: true, reverseData: false}}/>
            ),
            onCell: (_) => ({}),
            width: '70px'
        },
        {
            title: 
                <div>
                    <span><FormattedMessage id='juridical'/></span>
                    <ChevronDown className='mg-left-10'/> 
                </div>,
            dataIndex: 'juridical',
            render: (item) => (
                <CustomCellLayout item={item} layout={{cols: 1, isMultipleRow: true, reverseData: false}}/>
            ),
            onCell: (_) => ({}),
            width: '50px'
        },
        {
            title: 
                <div>
                    <span><FormattedMessage id='media'/></span>
                    <ChevronDown className='mg-left-10'/>
                </div>,
            dataIndex: 'media',
            render: (item) => (
                <CustomCellLayout item={item} layout={{cols: 1, isMultipleRow: true, reverseData: true}}/>
            ),
            onCell: (_) => ({}),
            width: '50px'
        },
        {
            title: 
                <div>
                    <ChevronSort className='chevron-sort-icon'/>
                    <span><FormattedMessage id='score'/></span>
                    <ChevronDown className='mg-left-10'/>
                </div>,
            dataIndex: 'score',
            render: (_) => (
                <CustomCellLayout item={_} layout={{cols: 1, isMultipleRow: true, reverseData: false}}/>
            ),
            onCell: (_) => ({}),
            width: '50px'
        },
        {
            title: '',
            dataIndex: 'action',
            fixed: 'right',
            render: (_, record) => (
                <Dropdown   className='check-dropdown' 
                            overlay={() => actionMenu(record, setItemRemoved)} placement='bottomRight' arrow 
                            onClick={e => e.preventDefault()}>
                    <a className='ant-dropdown-link' onClick={e => e.preventDefault()}>
                        <ThreeDot size={15} className='three-dot-icon'/>
                    </a>
                </Dropdown>
            ),
            width: '15px'
        },
    ];

    // Selected items
    const [selectedRows, setSelectedRows] = useState([]);

    // Selected items
    const [selectedRowKeys, setSelectedRowKeys] = useState([]);

    // Selected page
    const [currentPage, setCurrentPage] = useState(0);

    // Total page
    const [totalPage, setTotalPage] = useState(0);

    // Selected values from filter
    const [selectedFilterValues, setSelectedFilterValues] = useState({});

    // State for sidebar
    const [sidebarVisibility, setSidebarVisibility] = useState(false);


    // Table config
    const cols = ConfigData.tableColumns;

    const groups = ConfigData.groupByTableColumn;

    // State for fill data
    var [dataFill, setDataFill] = useState([]);

    function fetchData() {
        // Fetch data from CleanedList API
        RealEstateServices.getList(
            {[REQUEST_CONFIG.method]: 'post', 
            [REQUEST_CONFIG.path]: ENDPOINTS.RES_CLEANED_LIST}, 
            selectedFilterValues, 
            currentPage)
        .then(res => {
            var results = response.groupProperties(res.data, cols, groups)

            setTotalPage(res.totalPage)

            if (results && results.length > 0) {
                dataFill = results.map((
                    item,
                    index
                ) => (
                    {
                        id: item.id ? item.id.realEstateRawId : 0,
                        address: item.address,
                        land:   item.land,
                        house: item.house,
                        juridical: item.juridical,
                        media:  item.media,
                        score: item.score,
                        status: item.status.approveStatus,
                        key: index
                }));
    
                setDataFill(dataFill);
            }
            
            return dataFill;
        }).catch(error => {
            console.error('Error during fetch data:', error)
        });
    }

    // Convert results
    useEffect(() => {
        fetchData()
    }, [currentPage, selectedFilterValues, itemRemoved]);

    if (!dataFill) return null

    return(
        <Fragment>
            <CleanedResDataTable
                rowKey='id'
                tableColumns={columns}
                tableData={dataFill}
                selectedRows={selectedRows}
                setSelectedRows={setSelectedRows}
                selectedRowKeys={selectedRowKeys}
                setSelectedRowKeys={setSelectedRowKeys}
                totalRecords={dataFill.length}
                totalPages={totalPage}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                selectedFilterValues={selectedFilterValues}
                setSelectedFilterValues={setSelectedFilterValues}
                sidebarVisibility={sidebarVisibility}
                setSidebarVisibility={setSidebarVisibility}
                setItemRemoved={setItemRemoved}
            />
        </Fragment>
    )
}

export default injectIntl(ResCleanedList)