import React from "react";
import { FormattedMessage } from "react-intl";
import { Slider } from 'antd';
import { Row, Col } from 'antd';

function ResCollectionPoint({
    fromPoint,
    toPoint,
    selectedPoint,
    setSelectedPoint
}) {

    function onChange(value) {
        if (value.length > 0) {
            setSelectedPoint({
                startRealEstateCollectionPoint: value[0],
                endRealEstateCollectionPoint: value[1]
            })
        }
    }

    return (
        <div className="px-1 pt-3">
            <Row>
                <Col span={12}>
                    <div className="font-weight-bolder pb-1">
                        <FormattedMessage id="CollectionPoint" />
                    </div>
                </Col>
                <Col span={12}>
                    <Row>
                        <FormattedMessage id="From" />
                        <span className="font-weight-bolder" style={{marginLeft: '5px', marginRight: '5px'}}>
                            {selectedPoint?.startRealEstateCollectionPoint} <FormattedMessage id="Point" />
                        </span>

                        <FormattedMessage id="To" />
                        <span className="font-weight-bolder" style={{marginLeft: '5px'}}>
                            {selectedPoint?.endRealEstateCollectionPoint} <FormattedMessage id="Point" />
                        </span>
                    </Row>
                </Col>
            </Row>
            <Row>
                <Col span={1}>
                    <span className="font-weight-bolder">{Number(fromPoint)}</span>
                </Col>
                <Col span={22}>
                    <Slider
                        min={Number(fromPoint)}
                        max={Number(toPoint)}
                        range={true}
                        value={[selectedPoint?.startRealEstateCollectionPoint, selectedPoint?.endRealEstateCollectionPoint]}
                        step={9}
                        defaultValue={[117, 801]}
                        onChange={onChange}
                    />
                </Col>
                <Col span={1}>
                    <span className="font-weight-bolder">{Number(toPoint)}</span>
                </Col>
            </Row>
        </div>
    );
}

export default ResCollectionPoint;