import { useEffect, useState } from "react";
import { PropTypes } from 'prop-types';
import { injectIntl, FormattedMessage } from 'react-intl';
import CustomPagination from '@components/customPaginationV1';
import requestService from "../../../services/request";
import { countNotEmptyProperties } from '@src/helper/object';
import { ReactComponent as Home } from '../../../assets/images/icons/home.svg';
import { ReactComponent as Search } from '../../../assets/images/icons/search.svg';
import { ReactComponent as Filter } from '../../../assets/images/icons/filter.svg';
import { ReactComponent as ChevronDown } from '../../../assets/images/icons/chevron-down.svg';

import { Table, Menu, Dropdown, Button, message } from 'antd';
import useDebounce from "../../../utility/hooks/useDebounce";

import Sidebar from "@components/sidebar";
import CleanedFilter from "@components/filters/filterComponents/CleanedFilter";
import { notification } from "antd";
import "antd/dist/antd.css";

const CleanedResDataTable = ({
    tableColumns,
    tableData,
    selectedRows,
    setSelectedRows,
    selectedRowKeys,
    setSelectedRowKeys,
    totalRecords,
    totalPages,
    currentPage,
    setCurrentPage,
    selectedFilterValues,
    setSelectedFilterValues,
    sidebarVisibility,
    setSidebarVisibility,
    intl,
    setItemRemoved,
}) => {

    // Searching
    const [searchTerm, setSearchTerm] = useState("")
    useEffect(()=>{
        setSelectedFilterValues({
        ...selectedFilterValues,
        searchText: searchTerm,
        })
    }, [searchTerm])
    
    const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
            setSelectedRowKeys(selectedRowKeys)
            setSelectedRows(selectedRows)
        }
    }

    // Handle event when click on multi actions dropdown
    const onClick = ({ key }) => {
        // message.info(`Click on item ${key}`, 2.5);
        // setItemRemoved(prev => !prev)
        // TODO: call api to move selected res to picked list
        const idList = selectedRows.map((row) => row.id);
        requestService
            .send({
                method: "POST",
                path: "RealEstateRaw/pickCleaned",
                data: {
                    pickedListId: idList,
                },
            })
            .then(() => {
                notification.info({
                    message:
                        intl.formatMessage(
                            { id: "ResXMovedTo" },
                            { x: `${idList.join()}` }
                        ) + intl.formatMessage({ id: "pickedRes" }),
                });
            })
            .then(() => {
                setItemRemoved(prev => !prev);
            });
        // console.log(idList)
        // setItemRemoved(prev => !prev);
    };

    const onChange = (pagination, filters, sorter, extra) => {
    }

    const menu = (
        <Menu onClick={onClick}>
            <Menu.Item key='1'><FormattedMessage id='pickRes'/></Menu.Item>
        </Menu>
    );

    function toggleSidebar() {
        setSidebarVisibility(!sidebarVisibility);
    }

    return(
        <div className='res-cleaned-table'>
            <div className='res-cleaned-table-head'>
                <div className='res-cleaned-table-head-line'>
                    <div className='res-cleaned-table-head-line-left'>
                        <span className='title-wrapper font-w600'>
                            <Home className='home-icon'/>&ensp;
                            {intl.formatMessage({ id: 'cleanedRes' })}
                        </span>
                        <span className='mg-17-53 total-wrapper font-italic font-w900'><FormattedMessage id='XTotal' values={{ x: totalRecords }} /> {}</span>
                    </div>
                    <div className='d-flex align-items-center'>
                        <div className='res-input-wrapper'>
                            <Search color='white !important' />{' '}
                            <input
                                className='input-box'
                                placeholder={intl.formatMessage({ id: 'enterKeyword' })}
                                value={searchTerm}
                                onChange={({ target: { value } }) =>
                                setSearchTerm(value)
                                }
                            />
                        </div>
                        <div className='filter-btn-wrapper'>
                            <Button className='btn-box' onClick={toggleSidebar}>
                                <Filter className='mr-1' />{' '}
                                <FormattedMessage id='filter' />(
                                {countNotEmptyProperties(selectedFilterValues)})
                            </Button>
                        </div>
                    </div>
                </div>
                <div className='res-cleaned-table-head-line'>
                    <div className='selected-wrapper font-italic font-w600'>
                        <FormattedMessage id='selectedXItems' values={{ x: selectedRows.length }}/> &ensp;
                        <Dropdown   className='multi-actions-dropdown' 
                                    overlay={menu} placement='bottomRight' arrow 
                                    onClick={e => e.preventDefault()}>
                            <Button className='pick-action-btn'><FormattedMessage id='pickAction'/>&ensp;{<ChevronDown/>}</Button>
                        </Dropdown>
                    </div>
                </div>
            </div>
            <Table
                style={{ borderRadius: '24px' }}
                rowSelection={rowSelection}
                rowKey='id'
                pagination={false}
                onChange={onChange}
                columns={tableColumns}
                dataSource={tableData}
            />
            <CustomPagination
                totalPages={totalPages}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
            />
            <Sidebar
                open={sidebarVisibility}
                toggleSidebar={toggleSidebar}
                closeBtn={false}
                width="500"
                title=""
            >
                <CleanedFilter
                    values={selectedFilterValues}
                    setValues={setSelectedFilterValues}
                />
            </Sidebar>
        </div>
    )
}

export default injectIntl(CleanedResDataTable)

CleanedResDataTable.propTypes = {
    tableHeader: PropTypes.arrayOf(PropTypes.object),
    tableData: PropTypes.arrayOf(PropTypes.object),
    selectedRows: PropTypes.arrayOf(PropTypes.object),
    setSelectedRows: PropTypes.func,
    totalRecords: PropTypes.number,
    recordsPerPage: PropTypes.number,
    totalPages: PropTypes.number,
    currentPage: PropTypes.number,
    setCurrentPage: PropTypes.func,
};

CleanedResDataTable.defaultProps = {
    tableHeader: [],
    tableData: [],
    selectedRows: [],
    setSelectedRows: () => {},
    totalRecords: 0,
    recordsPerPage: 10,
    totalPages: 1,
    currentPage: 0,
    setCurrentPage: () => {},
    selectedFilterValues: {},
    setSelectedFilterValues: () => {},
};