import FilterService from "@src/services/filter";
import React, { useEffect, useState } from "react";
import ResAddressFilter from "../complexComponents/ResAddressFilter";
import ResLandFilter from "../complexComponents/ResLandFilter";
import ResTypeFilter from "../complexComponents/ResTypeFilter";
import ResHouseFilter from "../complexComponents/ResHouseFilter";
import ResJuridicalFilter from "../complexComponents/ResJuridicalFilter";
import ResCollectionPoint from "../complexComponents/ResCollectionPoint";
import { FormattedMessage } from "react-intl";
import { Button } from "antd";
import "antd/dist/antd.css";
import "./styles/CleanedFilter.scss";

function CleanedFilter({ values, setValues }) {
    // State for selected value from filter
    const [selectedResCategory, setSelectedResCategory] = useState(
        values?.filter?.realEstateCategoryId
    );
    const [selectedAddress, setSelectedAddress] = useState({
        district: values?.filter?.areaDistrictId,
        province: values?.filter?.areaProvinceId,
        ward: values?.filter?.areaWardId,
        details: values?.filter?.realEstateLocationHomeNumber,
    });
    const [selectedLandStat, setSelectedLandStat] = useState({
        length: {
            start: values?.filterClause?.startRealEstateLandLongs,
            end: values?.filterClause?.endRealEstateLandLongs,
        },
        width: {
            start: values?.filterClause?.startRealEstateLandWidth,
            end: values?.filterClause?.endRealEstateLandWidth,
        },
        area: {
            start: values?.filterClause?.startRealEstateLandDefaultSquare,
            end: values?.filterClause?.endRealEstateLandDefaultSquare,
        },
        shape: values?.filter?.realEstateLandShapeName,
    });
    const [selectedHouseStat, setSelectedHouseStat] = useState({
        floorArea: {
            start: values?.filterClause?.startRealEstateHouseFloorsSquare,
            end: values?.filterClause?.endRealEstateHouseFloorsSquare,
        },
        floors: values?.filter?.realEstateHouseFloors,
        bedrooms: values?.filter?.realEstateHouseBedRooms,
        wcs: values?.filter?.realEstateHouseToilets,
    });
    const [selectedJuridicalStat, setSelectedJuridicalStat] = useState({
        paper: values?.filter?.realEstateJuridicalName,
        juridical: values?.filter?.realEstateJuridicalId,
    });
    const [selectedPoint, setSelectedPoint] = useState({
        startRealEstateCollectionPoint: values?.filterClause?.startRealEstateCollectionPoint ? 
                                        values?.filterClause?.startRealEstateCollectionPoint : 117,
        endRealEstateCollectionPoint: values?.filterClause?.endRealEstateCollectionPoint ? 
                                      values?.filterClause?.endRealEstateCollectionPoint : 801
    });

    // State for filter options
    const [resTypeOptions, setResTypeOptions] = useState(null);
    const [provinces, setProvinces] = useState(null);
    const [districts, setDistricts] = useState(null);
    const [wards, setWards] = useState(null);
    const [shapes, setShapes] = useState(null);
    const [paperTypes, setPaperTypes] = useState(null);
    const [juridicalStatus, setJuridicalStatus] = useState(null);
    const DEFAULT_START_COLLECTION_POINT = 117;
    const DEFAULT_END_COLLECTION_POINT = 801;

    // NOTE: Clear filters
    function clearFilters() {
        setSelectedResCategory(null);
        setSelectedAddress(null);
        setSelectedLandStat(null);
        setSelectedHouseStat(null);
        setSelectedJuridicalStat(null);
        setSelectedPoint({
            startRealEstateCollectionPoint: DEFAULT_START_COLLECTION_POINT,
            endRealEstateCollectionPoint: DEFAULT_END_COLLECTION_POINT
        });
    }

    // TODO: Apply filters
    function applyFilters() {
        setValues({
            filter: {
                realEstateCategoryId: selectedResCategory,
                areaProvinceId: selectedAddress?.province,
                areaDistrictId: selectedAddress?.district,
                areaWardId: selectedAddress?.ward,
                realEstateLocationHomeNumber: selectedAddress?.details,
                realEstateLandShapeName: selectedLandStat?.shape,
                realEstateHouseFloors: selectedHouseStat?.floors,
                realEstateHouseBedRooms: selectedHouseStat?.bedrooms,
                realEstateHouseToilets: selectedHouseStat?.wcs,
                realEstateJuridicalName: selectedJuridicalStat?.paper,
                // realEstateJuridicalId: selectedJuridicalStat?.juridical,
            },
            filterClause: {
                startRealEstateLandLongs: selectedLandStat?.length?.start,
                endRealEstateLandLongs: selectedLandStat?.length?.end,
                startRealEstateLandWidth: selectedLandStat?.width?.start,
                endRealEstateLandWidth: selectedLandStat?.width?.end,
                startRealEstateLandDefaultSquare: selectedLandStat?.area?.start,
                endRealEstateLandDefaultSquare: selectedLandStat?.area?.end,
                startRealEstateHouseFloorsSquare:
                    selectedHouseStat?.floorArea?.start,
                endRealEstateHouseFloorsSquare:
                    selectedHouseStat?.floorArea?.end,
                startRealEstateCollectionPoint: selectedPoint?.startRealEstateCollectionPoint,
                endRealEstateCollectionPoint: selectedPoint?.endRealEstateCollectionPoint
            },
        });
    }

    // TODO: fetch metadata from server
    // NOTE: fetch post types
    useEffect(() => {
        // FilterService.getPostTypes().then((result) => {
        //     setResTypeOptions(result);
        // });
        FilterService.getCategories().then((result) => {
            setResTypeOptions(result);
        });
    }, []);

    // NOTE: Fetch cities list
    useEffect(() => {
        FilterService.getCities().then((result) => {
            setProvinces(result);
            setSelectedAddress({
                ...selectedAddress,
                province: null,
                district: null,
                ward: null,
            });
        });
    }, []);

    // NOTE: Fetch districts list
    useEffect(() => {
        if (selectedAddress?.province) {
            FilterService.getDistricts(selectedAddress?.province).then(
                (result) => {
                    setDistricts(result);
                    setSelectedAddress({
                        ...selectedAddress,
                        district: null,
                        ward: null,
                    });
                }
            );
        }
    }, [selectedAddress?.province]);

    // NOTE: Fetch wards list
    useEffect(() => {
        if (selectedAddress?.district) {
            FilterService.getWards(selectedAddress?.district).then((result) => {
                setWards(result);
                setSelectedAddress({
                    ...selectedAddress,
                    ward: null,
                });
            });
        }
    }, [selectedAddress?.district]);

    // NOTE: Fetch land shape list
    useEffect(() => {
        FilterService.getLandShapes().then((result) => {
            setShapes(result);
        });
    }, []);

    // NOTE: Fetch paper types
    useEffect(() => {
        FilterService.getPaperTypes().then((result) => {
            setPaperTypes(result);
        });
    }, []);

    // NOTE: Fetch juridical status
    useEffect(() => { 
        FilterService.getJuridicalStatus().then((result) => {
            setJuridicalStatus(result);
        });
    }, []);

    return (
        <div className="d-flex flex-column justify-content-between h-75">
            <div className="filter-title">
                <FormattedMessage id="filterRes" />
            </div>

            <div className="pb-4">
                {/* TODO: Res type */}
                <ResTypeFilter
                    resTypeOptions={resTypeOptions}
                    selectedResType={selectedResCategory}
                    setSelectedResType={setSelectedResCategory}
                />
                {/* TODO: Area and address */}
                <ResAddressFilter
                    addressData={{ provinces, districts, wards }}
                    selectedAddress={selectedAddress}
                    setSelectedAddress={setSelectedAddress}
                />
                {/* TODO: Land stat */}
                <ResLandFilter
                    landData={{ shapes }}
                    selectedLandStat={selectedLandStat}
                    setSelectedLandStat={setSelectedLandStat}
                />
                {/* TODO: House stat */}
                <ResHouseFilter
                    selectedHouseStat={selectedHouseStat}
                    onHouseStatChange={setSelectedHouseStat}
                />

                {/* TODO: Juridical */}
                <ResJuridicalFilter
                    juridicalData={{
                        papers: paperTypes,
                        juridicals: juridicalStatus,
                    }}
                    selectedJuridicalStat={selectedJuridicalStat}
                    setSelectedJuridicalStat={setSelectedJuridicalStat}
                />
                {/* TODO: Collection Point */}
                <ResCollectionPoint
                    fromPoint='0'
                    toPoint='999'
                    selectedPoint={selectedPoint}
                    setSelectedPoint={setSelectedPoint}
                />
            </div>

            {/* Action button */}
            <div className="d-flex justify-content-between px-1 pt-2 btn-container">
                <Button className="btn-clear" id="clearCleanedFilter" onClick={clearFilters}>
                    <FormattedMessage id="clearFilter" />
                </Button>
                <Button className="btn-apply" id="applyCleanedFilter" onClick={applyFilters}>
                    <FormattedMessage id="applyCleanedFilter" />
                </Button>
            </div>
        </div>
    );
}

export default CleanedFilter