import { useState, useEffect } from 'react';
import { injectIntl } from 'react-intl';
import { isObjEmpty } from '../../../utility/Utils';
import { Row, Col } from 'antd';
import 'antd/dist/antd.css';
import '@styles/base/pages/page-res-cleaned.scss';

const DEFAULT_LAYOUT = {cols: 1, isMultipleRow: false, reverseData: false} // 1 - one row, 2 - two rows

const CustomCellLayout = ({item = {}, layout = DEFAULT_LAYOUT, intl}) => {
    const [data] = useState(Object.entries(item || {}));

    if (isObjEmpty(data)) return <></>

    return (
        <div className='custom-layout'>
            {(layout.cols === 1 && !layout.isMultipleRow) && data.filter(item => item[1] !== null || item[1] !== undefined).map((item) => 
                layout.reverseData === true ? [intl.formatMessage({ id: item[0] }), item[1]].reverse().join(' ') :
                                              [intl.formatMessage({ id: item[0] }), item[1]].join(' ')
            ).join(', ')}

            {(layout.cols > 1 || (layout.isMultipleRow && layout.cols === 1)) && <Row>{data.map((item, index) => <Col key={index} span={Math.ceil(24 / layout.cols)}>{intl.formatMessage({ id: item[0]}, {x: item[1]})}</Col>)}</Row>}
        </div>
    )
}

export default injectIntl(CustomCellLayout)